import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';

import { GrnRoutingModule } from './grn-routing.module';
import { GrnComponent } from './grn.component';
import { GrnAddComponent } from './grn-add/grn-add.component';
import { GrnEditComponent } from './grn-edit/grn-edit.component';
import { GrnViewComponent } from './grn-view/grn-view.component';

@NgModule({
  imports: [
    CommonModule,
    CommonModule,
    SharedModule,
    GrnRoutingModule
  ],
  entryComponents: [
    GrnAddComponent,
    GrnEditComponent,
    GrnViewComponent
  ],
  declarations: [
    GrnComponent, 
    GrnAddComponent, 
    GrnEditComponent,
    GrnViewComponent
  ],
  schemas: [ NO_ERRORS_SCHEMA ]
})
export class GrnModule { }



