import { Component, OnInit, Input, AfterViewInit } from '@angular/core';
import { DirectGrnService } from '../../../../shared';

@Component({
  selector: 'app-grn-view',
  templateUrl: './grn-view.component.html',
  styleUrls: ['./grn-view.component.scss']
})
export class GrnViewComponent implements OnInit  {

  @Input() grnItems;
  
  public items: any;

  billSunArray:any = [];
  billSundrys:any = [];


  constructor(
    private grn: DirectGrnService
  ) { }

  ngOnInit() {
    //this.grnItems.id
    this.grn.getGrnById(this.grnItems.id).subscribe(
      (result: any) => {
        this.items = result.data[0];

        for(let i = 0; i < this.items.grn_items.length; i++){
          // this.billSundrys[i].
          for(let j = 0; j < this.items.grn_items[i].grn_bill_sundry_details.length; j++ ){
            let billId = this.items.grn_items[i].grn_bill_sundry_details[j].bill_sundry_id;
            let index = this.billSunArray.indexOf(billId);
            let data = {
              name : this.items.grn_items[i].grn_bill_sundry_details[j].bill_sundry.name,
              value : this.items.grn_items[i].grn_bill_sundry_details[j].value
            }

            if(index < 0){
              this.billSunArray.push(billId);
              this.billSundrys[j] = {};
              this.billSundrys[j].name = this.items.grn_items[i].grn_bill_sundry_details[j].bill_sundry.name
              this.billSundrys[j].array = [];
              this.billSundrys[j].array.push(data);
            }else{
              this.billSundrys[index].array.push(data);
            }
          }
        }

      }, error => {
        console.log(error);
      }
    )
  }
}



